$(document).ready( function () {
    // 
    $('#eskiz-bt').click(function () {
        $('#eskiz').arcticmodal();
    });
    $('#workproject-bt').click(function () {
        $('#workproject').arcticmodal();
    });
    $('#injener-bt').click(function () {
        $('#injener').arcticmodal();
    });
    $('#desinjer-bt').click(function () {
        $('#desinjer').arcticmodal();
    });
    $('.zvonok').click( function (e) {
        e.preventDefault();
        $('#exampleModal').arcticmodal();
    });

    $('#konf').click( function (e) {
        e.preventDefault();
        $('#konf-mod').arcticmodal();
    });


    //  
   $('input[name="phone"]').mask("+7(999) 999-99-99");

    //
    $('#carousel1').flexslider({
        animation: "slide",
        controlNav: false,
        animationLoop: false,
        slideshow: false,
        itemWidth: 210,
        itemMargin: 5,
        asNavFor: '#slider1'
    });

    $('#slider1').flexslider({
        animation: "slide",
        controlNav: false,
        animationLoop: false,
        slideshow: false,
        sync: "#carousel1",
        prevText: "",
        nextText: ""
    });

    $('#carousel2').flexslider({
        animation: "slide",
        controlNav: false,
        animationLoop: false,
        slideshow: false,
        itemWidth: 210,
        itemMargin: 5,
        asNavFor: '#slider2'
    });

    $('#slider2').flexslider({
        animation: "slide",
        controlNav: false,
        animationLoop: false,
        slideshow: false,
        sync: "#carousel2",
        prevText: "",
        nextText: ""
    });


    $('#carousel3').flexslider({
        animation: "slide",
        controlNav: false,
        animationLoop: false,
        slideshow: false,
        itemWidth: 210,
        itemMargin: 5,
        asNavFor: '#slider3'
    });

    $('#slider3').flexslider({
        animation: "slide",
        controlNav: false,
        animationLoop: false,
        slideshow: false,
        sync: "#carousel3",
        prevText: "",
        nextText: ""
    });


    // 

    var h_hght = $('header').innerHeight(); //  
    console.log(h_hght);
    var h_mrg = 0;    //      

    $(function(){

        var elem = $('.menu');
        var top = $(this).scrollTop();

        if(top > h_hght){
            elem.css('top', h_mrg);
        }

        $(window).scroll(function(){
            top = $(this).scrollTop();

            if (top+h_mrg < h_hght) {
                // elem.css('top', (h_hght-top));
            } else {
                elem.css('top', h_mrg);
            }
        });

    });


    //  
    (function(){
        var blocks = [];
        $('nav a[href^="#"]').each(function(){ //     '#'
            blocks.push(this.hash)
        })
        $blocks = $(blocks.join(',')) //   
        $blocks.each(function(){ //   ,    
            $elem = $(this)
            $elem.data('top', $elem.offset().top)
            $elem.data('height', $elem.outerHeight())
            $elem.data('bottom', $elem.data('top') + $elem.data('height'))
        })

        var $window = $(window)
        var biggestElement = null
        var biggestElementVisibleHeight = 0

        $(window).on('scroll', function(){ //  
            $blocks.each(function(i, el){ //  
                var screenTop = $window.scrollTop(); //   
                var screenBottom = screenTop + $window.height();

                var $elem = $(el)
                //        
                var visibleHeight = $elem.data('height')
                    - (screenTop > $elem.data('top') ? screenTop - $elem.data('top') : 0)
                    - ($elem.data('bottom') > screenBottom ? $elem.data('bottom') - screenBottom : 0)

                if (biggestElement == el.id) { //       ,
                    //      
                    biggestElementVisibleHeight = visibleHeight
                } else {
                    //         
                    if (visibleHeight > biggestElementVisibleHeight) {
                        biggestElementVisibleHeight = visibleHeight //  
                        biggestElement = el.id //   

                        $('nav a[href="#'+el.id+'"]') //    
                            .addClass('active')
                            .siblings().removeClass('active')
                    }
                }
            })
        })
    })();




    // 

    $('nav a, .cat-link-js').click( function(){ //       go_to
        var scroll_el = $(this).attr('href'); //    href,   , ..    #  .
        if ($(scroll_el).length != 0) { //      
            $('html, body').animate({ scrollTop: $(scroll_el).offset().top }, 500); //     scroll_el
        }
        return false; //   
    });


    //  
    $( '.lig' ).imageLightbox(
        {
            arrows:   true,
            button:   true
        }
    );
    $( '.lig2' ).imageLightbox(
        {
            arrows:   true,
            button:   true
        }
    );
    $( '.lig3' ).imageLightbox(
        {
            arrows:   true,
            button:   true
        }
    );
    //


});